import React from "react"
import "./program5b.css"
import { useCMS } from "../../contexts/CMSContext"

const Page5bChuongTrinhCaoDang: React.FC = () => {
  const { displayData } = useCMS()
  const content = displayData.pages.page5.chuongTrinhCaoDangContent
  const { title, imageDesktop, meta, curriculum } = content

  const imgOnLeft = (content.imagePosition || 'left') === 'left'

  return (
    <section className="page5b-section">
      <div className="page5b-container">

        {/* IMAGE */}
        <div className="page5b-image" style={{ order: imgOnLeft ? 1 : 2 }}>
          <img src={imageDesktop} alt={title} />
        </div>

        {/* CONTENT */}
        <div className="page5b-content" style={{ order: imgOnLeft ? 2 : 1 }}>
          <h2 className="page5b-title page5b-title-desktop">{title}</h2>
          <div className="page5b-underline page5b-underline-desktop" />

          <div className="page5b-meta">
            <p>{meta.duration}</p>
            {meta.schedule.map((line: string, i: number) => (
              <p key={i}>{line}</p>
            ))}
          </div>

          <div className="page5b-list">
            <ul>
              {curriculum.left.map((item: string, i: number) => (
                <li key={`left-${i}`}>{item}</li>
              ))}
            </ul>

            <ul>
              {curriculum.right.map((item: string, i: number) => (
                <li key={`right-${i}`}>{item}</li>
              ))}
            </ul>
          </div>
        </div>

      </div>
    </section>
  )
}

export default Page5bChuongTrinhCaoDang
